import React  from 'react';
import { useState } from 'react';

function Box({ value, onBoxClick }) {
  return ( <button className="box" onClick={onBoxClick}>{value}</button> );
}

function Board({ x, box, play }) {
  function click(i) {
    if (calculate(box) || box[i]) 
    {
      return;
    }
    const nextBox = box.slice();
    if (x) 
    {
      nextBox[i] = 'X';
    } 
    else 
    {
      nextBox[i] = 'O';
    }
    play(nextBox);
  }

  const winner = calculate(box);
  let move;
  if (winner) 
  {
    move = 'Winner: ' + winner;
  } 
  else 
  {
    move = 'Next player: ' + (x ? 'X' : 'O');
  }

  return (
    <div>
      <div className="move">{move}</div>
      <div className="board-row">
        <Box value={box[0]} onBoxClick={() => click(0)} />
        <Box value={box[1]} onBoxClick={() => click(1)} />
        <Box value={box[2]} onBoxClick={() => click(2)} />
      </div>
      <div className="board-row">
        <Box value={box[3]} onBoxClick={() => click(3)} />
        <Box value={box[4]} onBoxClick={() => click(4)} />
        <Box value={box[5]} onBoxClick={() => click(5)} />
      </div>
      <div className="board-row">
        <Box value={box[6]} onBoxClick={() => click(6)} />
        <Box value={box[7]} onBoxClick={() => click(7)} />
        <Box value={box[8]} onBoxClick={() => click(8)} />
      </div>
    </div>
  );
}

export default function Game() {
  const [history, setHistory] = useState([Array(9).fill(null)]);
  const [currentMove, setCurrentMove] = useState(0);
  const x = currentMove % 2 === 0;
  const currentBox = history[currentMove];

  function handlePlay(nextBox) {
    const nextHistory = [...history.slice(0, currentMove + 1), nextBox];
    setHistory(nextHistory);
    setCurrentMove(nextHistory.length - 1);
  }

  function jumpTo(nextMove) {
    setCurrentMove(nextMove);
  }

  const moves = history.map((box, nextStep) => {
    let text;
    if (nextStep > 0) 
    {
      text = 'Go to move #' + nextStep;
    } 
    else 
    {
      text = 'Go to game start';
    }
    return (
      <li key={nextStep}>
        <button onClick={() => jumpTo(nextStep)}>{text}</button>
      </li>
    );
  });

  return (
    <div className="game">
      <div className="game-board">
        <Board x={x} box={currentBox} play={handlePlay} />
      </div>
      <div className="game-info">
        <ol>{moves}</ol>
      </div>
    </div>
  );
}

function calculate(box) {
  const lines = [
    [0, 1, 2],
    [3, 4, 5],
    [6, 7, 8],
    [0, 3, 6],
    [1, 4, 7],
    [2, 5, 8],
    [0, 4, 8],
    [2, 4, 6],
  ];
  for (let i = 0; i < lines.length; i++) {
    const [a, b, c] = lines[i];
    if (box[a] && box[a] === box[b] && box[a] === box[c])
   {
      return box[a];
    }
  }
  return null;
}



